 * Petri Net Editor - Icon Styles
 * This file contains all the icon styles for palette and context pad
 */

/* ============================================
   PALETTE ICONS
   ============================================ */

.palette-icon-lasso-tool {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.palette-icon-create-arrow {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat center center;
  background-size: contain;
}

.palette-icon-labels {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) no-repeat center center;
  background-size: contain;
}

/* Active state for labels button - make SVG red when labels are hidden */
.palette-icon-labels.active {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_3___}) no-repeat center center !important;
  background-size: contain !important;
}

.palette-icon-create-transition {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.palette-icon-create-empty-transition {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_5___}) no-repeat center center;
  background-size: contain;
}

.palette-icon-create-circle {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_6___}) no-repeat center center;
  background-size: contain;
}

.palette-icon-start-simulation {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_7___}) no-repeat center center;
  background-size: contain;
}

/* Active state style only for simulation button - make SVG red */
.palette-icon-start-simulation.active {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_8___}) no-repeat center center !important;
  background-size: contain !important;
}

/* ============================================
   CONTEXT PAD ICONS
   ============================================ */

.context-pad-icon-remove {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_9___}) !important;
}

.context-pad-icon-connect {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_10___}) !important;
}

.context-pad-icon-place {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_6___}) no-repeat center center !important;
  background-size: 16px 16px !important;
}

.context-pad-icon-tool {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_11___}) no-repeat center center !important;
  background-size: 16px 16px !important;
}

.context-pad-icon-transition {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_4___}) no-repeat center center !important;
  background-size: 16px 16px !important;
}

.context-pad-icon-replace {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_12___}) no-repeat center center !important;
  background-size: 16px 16px !important;
}

